import About from "../components/About";
import Home from "../components/Home";
import Projects from "../components/Projects";
import Resume from "../components/Resume";

export default [
  {
    path: "/",
    component: Home,
  },
  {
    path: "/about",
    component: About,
  },
  {
    path: "/projects",
    component: Projects,
  },
  {
    path: "/resume",
    component: Resume,
  },
];
